function toggleLight(param){
    var buttonheadnav = document.querySelectorAll('.btnHeadNav');
    buttonheadnav.forEach(element => {
        if(element.classList.contains(param) && element.classList.contains('active')){
            element.style.display= "flex";
        }
        else if(element.classList.contains(param) && element.classList.contains('notActive')){
            element.style.display= "none";
        } else {
            if(element.classList.contains('active')){
                element.style.display= "none";
            } else if(element.classList.contains('notActive')){
                element.style.display= "flex";
            }
        }
    });
}
function rotateToEarn(){
    console.log("Going to Earn Section");
    // $('.owl-carousel').trigger("prev.owl.carousel");
    gsap.to(modelBWE.rotation, {duration: 2, x:0, y:-(((Math.PI)*2)/3)+0.3, z:0});
    gsap.to(modelBWE.position, {duration: 2, x:0 , y: -0.5, z:0});
    gsap.to(cameraBWE.position, {duration: 2, x:0, y: 7, z: 12});
    toggleLight('earn');
}

function rotateToWin(){
    console.log("Going to Win Section");
    // $('.owl-carousel').trigger("next.owl.carousel");
    gsap.to(modelBWE.rotation, {duration: 2, x:0, y:0.4, z:0});
    gsap.to(cameraBWE.position, {duration: 2, x:0, y: 7, z: 12});
    gsap.to(modelBWE.position, {duration: 2, x:0 , y: 0, z:0});
    toggleLight('win');
}
function rotateToBattle(){
    console.log("Going to Battle Section");
    // $('.owl-carousel').trigger("prev.owl.carousel");
    gsap.to(modelBWE.rotation, {duration: 2, x:0, y:(((Math.PI)*2)/3)+0.3, z:0});

    gsap.to(cameraBWE.position, {duration: 2, x:0, y: 7, z: 12});
    gsap.to(modelBWE.position, {duration: 2, x:0 , y: -0.5, z:0});
    toggleLight('battle');
}

$(".owl-carousel").on('changed.owl.carousel',function(property){
    var current = property.item.index - 1;
    console.log(current);
    if(current == 1 || current == 4)rotateToBattle();
    else if(current == 2)rotateToWin();
    else if(current==0 || current == 3)rotateToEarn();
});

document.getElementById('battle').addEventListener('click',rotateToBattle);

document.getElementById('win').addEventListener('click',rotateToWin);

document.getElementById('earn').addEventListener('click',rotateToEarn);



